    display: flex;
    color: $white;
}

.school-animation {
    width: 60vw;
    height: 100vh;

    video {
        width: 100%;
        margin-top: 65px;
        // box-shadow: 0px 0px 50px #333;

    }
}

.content-container {
    width: 500px;
    margin: 0 auto;
