*{margin:0;padding:0;box-sizing:border-box}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.main-page{font-family:"Exo 2",sans-serif;display:flex;color:#cfcfcf}.school-animation{width:60vw;height:100vh}.school-animation video{width:100%;margin-top:65px}.content-container{width:500px;margin:0 auto}.content-container .content{display:flex;flex-direction:column;padding-top:40px}.content-container .content .slogan{text-align:left;line-height:1.8em;font-size:24px}.a{position:absolute;width:100%;height:100%;z-index:-1}

.bm-burger-button{position:fixed;width:36px;height:30px;right:36px;top:36px}.bm-burger-bars{background-color:#c1232a}.bm-burger-bars-hover{background-color:#fff}.bm-cross-button{height:24px;width:24px}.bm-cross-button:hover .bm-cross{background-color:#c1232a}.bm-cross{background:#bdc3c7}.bm-menu-wrap{position:fixed;height:100%}.bm-menu{background:#111;padding:2.5em 1.5em 0;width:100%}.bm-morph-shape{fill:#111}.bm-item-list{color:#b8b7ad}.bm-item{display:inline-block;color:#cfcfcf;text-decoration:none;font-weight:700;padding:1em;font-size:18px}.bm-item:hover:not(.sub-title){color:#c1232a}.bm-item:hover:not(.sub-title) img{-webkit-filter:invert(100%);filter:invert(100%)}.bm-item img{width:28px;height:auto;vertical-align:middle;margin-right:15px}.sub-title{text-align:center;font-size:24px;margin-top:20px}.sub-title:focus{outline:none}.bm-overlay{background:rgba(0,0,0,0.3)}

.introduction{position:absolute;top:50px;left:230px;display:flex;justify-content:center;align-items:flex-start}.introduction .content{width:600px;box-sizing:border-box}.introduction .content h1{font-size:50px;font-weight:100;color:#c1232a;text-transform:uppercase}.introduction .content p{line-height:1.8;transform:translateY(-20px)}

.list h1{margin-bottom:30px;margin-left:100px;font-size:36px}.list .items{border-collapse:separate;border-spacing:100px 0}.list .item{vertical-align:top;padding:50px 100px 50px 60px;border-top:1px solid #c1232a;position:relative}.list .item .index{position:absolute;top:-1px;right:0;border-left:1px solid #c1232a;border-right:1px solid #c1232a;border-bottom:1px solid #c1232a;width:60px;height:60px;color:#c1232a;font-weight:500;font-size:22px;text-align:center;line-height:60px}.list .item .content{width:290px;margin:0 auto;line-height:1.8em}.list .item .content .item-title{text-transform:uppercase;letter-spacing:.1em;text-shadow:0px 0px 2px #555}

.table{width:1000px;line-height:1.8em;margin-bottom:50px}.table .head{height:150px;background-color:#353744;color:#cfcfcf;display:flex;justify-content:center;align-items:center;text-transform:uppercase;line-height:1.8em}.table .head img{width:48px;height:auto;margin-right:15px}.table .head .title{font-size:36px}.table .body{padding:50px 20px 20px;background-color:#2d2f3a}.table .body .item{width:33%;padding:0 40px;vertical-align:top}.table .body .item .content{padding-bottom:2em}.table .body .item .content .item-title{color:#c1232a}

.additional-text{width:800px;margin-bottom:150px;padding:20px;box-shadow:2px 2px 10px #2c2e3a;line-height:32px;font-size:16px}

.swal2-popup{width:70%;background-color:#111}.swal2-close:hover{color:#c1232a}.swal2-close:focus{box-shadow:none}.swal2-styled.swal2-confirm{background-color:#c1232a;font-family:"Exo 2",sans-serif}.swal2-input,.swal2-textarea{border-color:#cfcfcf;color:#cfcfcf;font-family:"Exo 2",sans-serif}.swal2-input:focus,.swal2-textarea:focus{box-shadow:none;border-color:red}.swal2-textarea{resize:none}.swal2-input::-webkit-input-placeholder, .swal2-textarea::-webkit-input-placeholder{color:#cfcfcf}.swal2-input:-ms-input-placeholder, .swal2-textarea:-ms-input-placeholder{color:#cfcfcf}.swal2-input::-ms-input-placeholder, .swal2-textarea::-ms-input-placeholder{color:#cfcfcf}.swal2-input::placeholder,.swal2-textarea::placeholder{color:#cfcfcf}.swal2-validation-message{font-family:"Exo 2",sans-serif}.swal2-title{color:#cfcfcf;font-family:"Exo 2",sans-serif;font-weight:normal;font-size:24px}.swal2-icon.swal2-success{border-color:#c1232a !important}.swal2-success-line-tip{background-color:#c1232a !important}.swal2-success-line-long{background-color:#c1232a !important}.swal2-success-ring{border-color:rgba(193,35,42,0.3) !important}.swal2-icon.swal2-error{border-color:#c1232a !important}.swal2-x-mark-line-left{background-color:#c1232a !important}.swal2-x-mark-line-right{background-color:#c1232a !important}

footer{width:calc(100% - 72px);position:fixed;left:36px;bottom:36px;display:flex;justify-content:space-between;align-items:flex-end}footer .logo{width:100px}footer .contacts{display:flex;flex-direction:column;align-items:center}footer .contacts .contact-info{display:flex;flex-direction:column;align-items:center}footer .contacts .contact-info .icon{width:28px;height:auto;margin-right:10px}footer .contacts .contact-info .icon:hover{-webkit-filter:invert(100%);filter:invert(100%)}footer .contacts .contact-info .telephones{line-height:1.6em;margin:5px 0;color:#c1232a;letter-spacing:2px}footer .contacts .map{color:rgba(255,255,255,0.9);cursor:pointer}footer .contacts .map:hover{color:rgba(255,255,255,0.5)}


.about-page{font-family:"Exo 2",sans-serif;display:flex;flex-direction:column;justify-content:center;align-items:center;grid-gap:30px;gap:30px;color:#cfcfcf;position:relative;opacity:.9999}.about-page .about-animation{position:relative}.about-page .about-animation video{width:100%}.about-page .introduction{height:100vh}.about-page .course-fee{margin:0}.about-page .btn-enter{margin:50px 0 150px}

.teacher-info{width:1000px;margin:20px 0 60px;display:flex;justify-content:space-between}.teacher-info img{width:500px;height:auto;margin-right:20px;border:2px solid #cfcfcf;border-top-left-radius:48px;border-bottom-right-radius:48px}.teacher-info .content h2{margin-bottom:10px}.teacher-info .content p{line-height:26px;font-size:16px}

body{background-color:#111}.page{font-family:"Exo 2",sans-serif;display:flex;flex-direction:column;justify-content:center;align-items:center;grid-gap:30px;gap:30px;color:#cfcfcf;position:relative;opacity:.9999}.page .about-animation{position:relative}.page .about-animation video{width:100%}.page .introduction{height:100vh}.btn-enter{height:50px;width:230px;margin:70px 0;box-sizing:border-box;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:transparent;border:2px solid #fff;font-size:1rem;line-height:1;letter-spacing:2px;text-decoration:none;text-transform:uppercase;font-weight:700;color:#c1232a;display:flex;justify-content:center;align-items:center;box-shadow:0 0 40px 40px #fff inset, 0 0 0 0 #fff;transition:all 150ms ease-in-out;cursor:pointer;font-family:"Exo 2",sans-serif}.btn-enter:hover{color:#fff;outline:0;box-shadow:0 0 10px 0 #fff inset, 0 0 10px 4px #fff}

